<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_MAIN,
    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'
    import UTIL from '@/common/util'
    import {mapGetters} from "vuex";

    export default {
        name: 'siteMain',
        computed: {
            ...mapGetters('config', ["getMenu", "getSettingActive"]),
        },
        watch: {
            getSettingActive(val) {
                if (val) {
                    this.getMcode();
                }
            }
        },
        data() {
            return {
                util: UTIL,
                noticeList: [],
                freeList: [],
                qnaList: [],
                uesList: [],
                commentList: [],
                boardIds: {},

            };
        },
        created() {
            this.getData();
            if (this.getSettingActive) {
                this.getMcode();
            }
        },
        methods: {
            getMcode() {

                for (let menu of this.getMenu) {
                    if (menu.muse != 'yes') continue;
                    if (menu.subList && menu.subList.length > 0) {
                        for (let subMenu of menu.subList) {
                            if (subMenu.muse != 'yes') continue;
                            if (subMenu.linkType == 'board') {
                                this.boardIds[subMenu.boardId] = {mainCode: menu.code, subCode: subMenu.code};
                            }
                        }

                    } else {
                        if (menu.linkType == 'board') {
                            this.boardIds[menu.boardId] = {mainCode: menu.code};
                        }
                    }
                }

            },
            getData() {
                apiService.post(API_URL_GET_MAIN, {}, (res) => {
                    if (res.status == 'success') {
                        this.noticeList = res.boardData.noticeData.list;
                        this.freeList = res.boardData.freeData.list;
                        this.qnaList = res.boardData.qnaData.list;
                        this.uesList = res.boardData.uesData.list;
                        this.commentList = res.commentData.list;
                    }
                })
            },
            articleDetail(item) {
                if (this.boardIds[item.bid]?.mainCode) {
                    const subCode = (this.boardIds[item.bid].subCode) ? this.boardIds[item.bid].subCode : 'none';
                    this.$router.push('/board/articleView/' + this.boardIds[item.bid].mainCode + '/' + subCode + '/' + item.id)
                }
            },
            commentDetail(item) {
                if (this.boardIds[item.bid]?.mainCode) {
                    sessionStorage.setItem('viewComment', item.id);

                    const subCode = (this.boardIds[item.bid].subCode) ? this.boardIds[item.bid].subCode : 'none';
                    this.$router.push('/board/articleView/' + this.boardIds[item.bid].mainCode + '/' + subCode + '/' + item.parentId)
                }

            },
            goHamoLink(cmd) {
                let url = '';
                switch (cmd) {
                    case 'userGuide':
                        url = 'https://docs.hamonikr.org/hamonikr-5.0';
                        break;
                    case 'tip':
                        url = 'https://docs.hamonikr.org/hamonikr-5.0/tips';
                        break;
                    case 'example':
                        url = 'https://hamonikr.org/board_nLnK17';
                        break;
                    case 'faq':
                        url = 'https://docs.hamonikr.org/hamonikr-5.0/faq';
                        break;
                    case 'baropam':
                        url = 'https://mc529.tistory.com/1400?category=5030';
                        break;

                    case 'askos':
                        url = 'https://askos.co.kr/questions/list';
                        break;
                    case 'down':
                        url = 'https://hamonikr.org/download_HANLA';
                        break;
                    case 'sw':
                        url = 'https://hamonikr.org/hamonikr_test';
                        break;
                }
                window.open(url)
            },


        },


    }
</script>

